import * as ApiPath from "api/ApiPath";
import WebApi from "api/WebApi";
export function Login(payload) {
    return WebApi.Post(ApiPath.login, payload);
}
export function Register(payload) {
    return WebApi.Post(ApiPath.register, payload);
}
export function Logout() {
    return WebApi.Post(ApiPath.logout, {});
}
export function WhoAmI() {
    return WebApi.Post(ApiPath.checkMe, {});
}
export default {
    Login,
    Register,
    Logout
};



